<template>
  <div id="exit" class="maskBox">
    <div class="confirmBox">
      <div class="flex-row title">{{config.title ? config.title: "你有奖励待领取"}}</div>
      <div class="text-tip">
        <div class="name-tip">{{carInfoData.brand_name}}就在眼前</div>
        <div class="usually">{{config.content ? config.content: "天天做任务，轻松换豪车"}}</div>
      </div>
      <div class="carImgbox flex-row">
        <div class="carImage iconSize">
          <img :src="carInfoData.thumb" alt="图片">
        </div>
      </div>
      <div class="buttonGroup flex-row jusB">
        <div class="cancel flex-row" @click="close('cancel')">{{config.cancelText ? config.cancelText: '不要了，退出'}}</div>
        <div class="doTask flex-row" @click="close('confirm')">{{config.confirmText ? config.confirmText : '再想想'}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    carInfo: {
      type: Object,
      default () {
        return null
      }
    },
    config: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      carInfoData: {}
    }
  },
  methods: {
    close (action) {
      this.$emit('closeConfirm', {action})
    },
    // 获取车的信息
    getCarInfo () {
      let carInfoData = null
      if (this.$utils.cache('carInfoData')) {
        carInfoData = JSON.parse(this.$utils.cache('carInfoData'))
        this.carInfoData = carInfoData
      }
      if (!carInfoData) {
        this.$api.get('SHARE_GUILDBROWERS', {}, res => {
          this.carInfoData = res.data
          this.$utils.cache('carInfoData', JSON.stringify(this.carInfoData))
        })
      }
    }
  },
  created () {
    if (this.carInfo) {
      this.carInfoData = this.carInfo
    } else {
      this.getCarInfo()
    }
  }
}
</script>
<style lang="scss" scoped>
#exit{
  .confirmBox{
    width: 600px;
    border-radius: 15px;
    overflow: hidden;
    font-size: 30px;
    .title{
      background-image: linear-gradient(-210deg, #FFD500 0%, #FFAA00 100%);
      height: 165px;
      position: relative;
      overflow: hidden;
      font-size: 44px;
      color: #FFFFFF;
      text-align: center;
      &::after{
        position: absolute;
        display: block;
        border-radius: 50%;
        content: '';
        width: 6000px;
        height: 6000px;
        left: 50%;
        transform: translateX(-50%);
        top: 140px;
        background: #fff;
      }
    }
    .text-tip{
      width: 530px;
      height: 84px;
      margin: 40px auto 20px;
      text-align: center;
      line-height: 36px;
    }
    .carImgbox{
      background: #D8D8D8;
      width: 500px;
      height: 240px;
      margin: 0 auto;
      overflow: hidden;
    }
    .carImage{
      height: 187px;
      width: 250px;
    }
    .buttonGroup{
      width: 500px;
      margin: 50px auto;
      user-select: none;
      >div{
        width: 230px;
        height: 80px;
        border-radius: 40px;
        box-sizing: border-box;
      }
      .cancel{
        border: 1px solid #979797;
        color: #232323;
      }
      .doTask{
        background: #FFAC00;
        color: #fff;
      }
    }
  }
}
</style>



