<template>
  <div id="replyList" v-if="item" :class="{isAndroid: $utils.isAndroid()}" ref="replyList">
    <div class="clear li">
      <div class="left fl">
        <div class="img">
          <img :src="item.avatar" alt=""
          onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
        </div>
      </div>
      <div class="right fl">
        <div class="p1 clear">
          <div class="fl">{{item.nickname}}</div>
        </div>
        <div class="p2">
          {{item.content}}
        </div>
        <div class="p3 clear">
          <div class="fl fl-time">{{$utils.timeFormat(item.comment_time*1000)}}</div>
          <div class="fl mL30 Reply">{{$utils.thousandRead(item.reply)}} 回复</div>
          <div class="fr">{{$utils.thousandRead(item.fabulous) === 0?'赞':$utils.thousandRead(item.fabulous)}}</div>
          <div class="fr thumbs mR15" @click="thumbFn">
            <img src="../../assets/img/article_huizan.png" v-if="item.is_fabulous===0" alt="">
            <img src="../../assets/img/zan_jiayi.png" v-if="item.is_fabulous!==0" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      全部回复
    </div>
    <div class="list" v-if="list.length!==0">
      <div class="clear li" v-for="(itemList, index) in list" :key="itemList.rid">
        <div class="left fl">
          <div class="img">
            <img :src="itemList.avatar" alt=""
            onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
          </div>
        </div>
        <div class="right fl">
          <div class="p1 clear">
            <div class="fl">{{itemList.nickname}}</div>
          </div>
          <div class="p2">
            {{$utils.trim(itemList.content)}}
          </div>
          <div class="p3 clear">
            <div class="fl">{{$utils.timeFormat(itemList.comment_time*1000)}}</div>
            <div class="fr">{{$utils.thousandRead(item.fabulous) === 0?'赞':$utils.thousandRead(item.fabulous)}}</div>
            <div class="fr thumbs mR15" @click="replyThumb(itemList, index)">
              <img src="../../assets/img/article_huizan.png" v-if="itemList.is_fabulous===0" alt="">
              <img src="../../assets/img/zan_jiayi.png" v-if="itemList.is_fabulous!==0" alt="">
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer clear">
      <div class="fl oDiv1" @click="closeFn">
        <div class="backIcon">
          <img src="../../assets/img/detail_back.png" alt="">
        </div>
      </div>
      <div class="fl input1">
        <input type="text" v-model="content" id="release" placeholder="回复评论..." @focus="inputFocus" @blur="inputBlur">
      </div>
      <div class="fl input2" @click="release">
        发布
      </div>
      <!-- 评论遮罩层 -->
      <div class="mask" v-if="inpuBol"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'replyList',
  props: {
    item: {
      type: Object,
      default: null
    },
    list: {
      type: Array,
      default: null
    },
    replyTotal: {
      type: Number,
      default: 0
    },
    details: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      inpuBol: false,        // input控制
      content: '',           // 回复内容
      page: 1                // 默认第一页
    }
  },
  methods: {
    // 发布评论的回复
    release () {
      if (this.content.trim().length === 0) {
        this.$toast({
          message: '请填写内容后发布',
          position: 'middle',
          duration: 1500
        })
        return
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'middle',
          duration: 1500
        })
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = this.item.cid
      data.content = this.content.trim()
      this.$api.post('REPLY_COMMENT', data, (res) => {
        this.item.reply++
        this.page = 1        // 恢复第一页
        this.$emit('getList', this.item)
        this.$nextTick(() => {
          this.$refs.replyList.scrollLeft = 0
          this.$refs.replyList.scrollTop = 0
        })
        this.content = ''
      })
    },
    // 回复点赞
    replyThumb (item, index) {
      if (item.is_fabulous === 1) {
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = this.item.cid
      data.rid = item.rid
      this.$api.post('FABULOUS', data, (res) => {
        this.list[index].is_fabulous = 1
        this.list[index].fabulous++
      })
    },
    // 内部评论点赞
    thumbFn () {
      if (this.item.is_fabulous === 1) {
        return
      }
      if (this.details.source.indexOf('今日头条') > -1) {
        this.item.is_fabulous = 1
        this.item.fabulous++
      } else {
        let data = {}
        data.aid = this.$route.params.aid * 1
        data.cid = this.item.cid
        this.$api.post('FABULOUS', data, (res) => {
          this.item.is_fabulous = 1
          this.item.fabulous++
        })
      }
    },
    // input失焦事件
    inputBlur () {
      this.inpuBol = false
    },
    // input聚焦事件
    inputFocus () {
      this.inpuBol = true
    },
    // 关闭弹窗
    closeFn () {
      this.$emit('closeReply')
    },
    // 滚动条事件
    upList () {
      if (this.scrHeight() === 0) {
        if (this.replyTotal > this.list.length) {
          this.page++
          this.$api.get('REPLY_LIST', {cid: this.item.cid, page: this.page}, (res) => {
            let lists = res.data.data
            this.$emit('addlist', lists)    // 添加列表
          })
        }
      }
    },
    // 滚动条距离底部高度
    scrHeight () {
      const oDiv = this.$refs.replyList
      let scrollTop = oDiv.scrollTop
      let clientHeight = 0
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
      } else {
        clientHeight = (document.body.clientHeight > document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
      }
      return oDiv.scrollHeight - scrollTop - clientHeight
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.$refs.replyList.onscroll = this.upList
    })
  }
}

</script>

<style lang="scss" scoped>
#replyList{
  width: 750px;
  height: 100%;
  background: #fff;
  padding: 0 20px;
  box-sizing: border-box;
  overflow-y: scroll;
  padding-bottom: 99px;
  .content{
    border-top: 1px solid #fa0;
    width: 120px;
    text-align: center;
    margin-top: -1px;
    font-size: 28px;
    padding: 10px 0;
  }
  .li{
    border-bottom: 1px #DADADA solid;
    padding-top: 20px;
  }
  .thumbs{
    width: 24px;
    height: 24px;
  }
  .right{
    padding-left: 10px;
    width: 615px;
    color: #a2a2a2;
    .p1{
      font-size: 30px;
    }
    .p2{
      font-size: 30px;
      word-wrap: break-word;
      color: #333;
      margin: 20px 0;
      line-height: 46px;
    }
    .p3{
      font-size: 26px;
      margin-bottom: 30px;
      line-height: 30px;
      >*{
        padding-top: 5px;
      }
    }
    .Reply{
      background: #F4F5F7;
      padding: 5px 18px;
      text-align: center;
      border-radius: 15px;
      font-size: 26px;
    }
  }
  .left{
    width: 80px;
    .img{
      width: 76px;
      height: 76px;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  img{
    width: 100%;
    height: 100%;
  }
  .footer{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: 0;
    height: 100px;
    background: #fff;
    width: 100%;
    line-height: 100px;
    box-sizing: border-box;
    margin-left: -20px;
    .mask{
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.3);
      position: absolute;
      bottom: 100px;
    }
    .input1{
      height: 100%;
      display: flex;
      align-items: center;
      input{
        height: 60px;
        width: 520px;
        border: none;
        background: #F0F2F1;
        border-radius: 30px;
        margin-right: 30px;
        text-indent: 30px;
      }
      input:focus{
        outline: none;
        resize: none;
      }
    }
    .oDiv3{
      width: 48px;
      height: 48px;
      padding-left: 25px;
      padding-right: 10px;
      padding-top: 10px;
    }
    .oDiv2{
      width: 515px;
      height: 60px;
      padding-top: 12px;
      padding-right: 10px;
    }
    .oDiv1{
      display: flex;
      align-items: center;
      padding: 0 20px 0 30px;
      height: 100%;
      .backIcon{
        width: 30px;
        display: flex;
        align-items: center;
        height: 52px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .bottom{
    bottom: -100px;
  }
}
</style>



