<template>
  <div class="downloadApp flex-row" v-if="$config.source_style < 6">
    <div class="leftIcon iconSize">
      <img src="../../assets/img/details/download_logo.png" alt="logo">
    </div>
    <div class="center">
      <div class="d-title">掘金宝资讯</div>
      <div class="d-text">看资讯影视，免费赚车赚房</div>
    </div>
    <div class="rightButton" :class="{isAndroid: $utils.isAndroid()}" @click="toDownload"><div class="check">打开</div></div>
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    toDownload () {
      if (this.$config.source_style === 6) {
        this.$utils.plusReady(plus => {
          plus.runtime.openURL(this.$config.iOSUrl)
        })
      } else if (this.$config.source_style === 7) {
        this.$utils.plusReady(plus => {
          plus.runtime.openURL(this.$config.iOSUrl)
        })
      } else {
        if (this.$utils.isIos()) {
          window.open(this.$config.iOSUrl)
        } else {
          window.open(this.$config.applicationUrl)
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.downloadApp {
  position: fixed;
  bottom: 100px;
  background: #fff;
  width: 100%;
  border-top: 1px solid #eee;
  box-sizing: border-box;
  justify-content: flex-start;
  box-shadow: 0 1px 2px 0 #E5E5E5, 0 -1px 2px 0 #E5E5E5;
  padding: 23px 27px 20px;
  box-sizing: border-box;
  .leftIcon {
    width: 87px;
    height: 87px;
    margin-right: 15px;
  }
  .center {
    text-align: left;
    flex: 1;
  }
  .d-title {
    font-size: 32px;
    line-height: 45px;
    color: #999;
  }
  .d-text {
    font-size: 30px;
    line-height: 40px;
    color: #333;
  }
  .rightButton {
    background:  #FFA100;
    border-radius: 36px;
    width: 152px;
    height: 66px;
    color: #fff;
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    justify-content: center;
    font-size: 26px;
    // font-weight: 900;
    box-sizing: border-box;
    .check {
      font-size: 32px;
      font-weight: 900;
      line-height: 40px;
    }
    .sqrt {
      border-color: #fff;
      width: 14px;
      margin-left: 4px;
      height: 14px;
      border-width: 4px;
    }
    &.isAndroid {
      padding-top: 8px;
      .sqrt {
        margin-top: -6px;
        height: 17px;
        width: 16px;
        border-width: 6px;
      }
    }
  }
}
</style>



