<template>
  <div class="article-lead" :class="{isMovie: isMovie === 1, isarticle: isMovie !== 1}">
    <div class="fxts" :class="{isarticlefxts: isMovie !== 1}"></div>
    <div class="conts f24" v-if="isMovie">{{'分享影视到微信群，能急速锁粉，狂赚掘金宝'}}</div>
    <div class="conts f24" v-else>
      <p class="pfirst">{{'分享文章到微信群，能极速锁粉，狂赚掘金宝'}}</p>
      <p class="second">{{'(仅支持微信锁粉，暂不支持QQ等其他方式)'}}</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    isMovie: {
      type: Number,
      default: 0
    }
  },
  created () {
    console.log(this.isMovie)
  }
}
</script>

<style lang="scss" scoped>
.article-lead{
  position: fixed;
  bottom: 100px;
  width: 622px;
  height: 101px;
  line-height: 60.5px;
  right: 50px;
  background: url("../../assets/img/bottom-word.png") no-repeat;
  background-size: 100%;
  text-align: center;
  overflow: hidden;
  &.isMovie{
    margin-right: -30px;
  }
  &.isarticle{
    height: 132px;
    bottom: 100px;
    text-align: center;
    background: url("../../assets/img/bigbottom-world.png") no-repeat;
    background-size: 100% 100%;
  }
  .fxts{
    width: 28px;
    height: 28px;
    background: url('../../assets/img/fxts.png') no-repeat;
    background-size: 100%;
    display: inline-block;
    margin-right: 10px;
    vertical-align: middle;
  }
  .isarticlefxts {
    width: 44px;
    height: 42px;
    background: url('../../assets/img/bigfxts.png') no-repeat;
    background-size: 100%;
    vertical-align: top;
    margin-top: 30px;
  }
  .conts{
    color: #fff;
    display: inline-block;
  }
  .pfirst {
    color:#fff;
    display: inline-block;
    line-height: 20px;
  }
  .second {
    color:#fff;
    font-size: 22px;
    line-height: 22px;
  }
}
</style>



