<template>
  <div id="hot-comment" ref="comment" v-if="details">
    <comment-list :list="list" :total='total' :articleAd4="articleAd4" :details="details"
     @replyFn="replyFn"></comment-list>
    <transition>
      <dialog-bottom @closeReply="replyBol=false" v-if="replyBol">
        <toutiaoReplyList
          :list="replyList"
          :details="details"
          @getList="replyFn"
          @getComment='getComment'
          :replyTotal="replyTotal"
          @closeReply="replyBol=false"
          @addlist ="addlist"
          :isLoadingReply='isLoadingReply'
          :item="replyItem"
        ></toutiaoReplyList>
      </dialog-bottom>
    </transition>
    <div class="footer clear" v-show="$config.source_style !== 6" :class="{'positioning':!inputBol}">
      <div class="fl oDiv1" @click="toIndex">
        <div class="backIcon">
          <img src="../../assets/img/detail_back.png" alt="">
        </div>
      </div>
      <label class="fl oDiv2" @click="setShowInput">
        <!-- <img src="../../assets/img/icon-input.png" alt=""> -->
        <div class="commentLogin" v-if="!commonState.login_bol" @click="isLogin"></div>
        <div class="txt">写评论...</div>
      </label>
      <div class="fl oDiv3" @click="commentTarget">
        <img src="../../assets/img/detail_message.png" alt="">
        <div class="num" v-if="total!==0">{{total > 99 ? '99+' : total}}</div>
      </div>
      <div class="fl oDiv4" @click="collectionBnt">
        <img src="../../assets/img/detail_collect.png" v-if="details.is_collection===0" alt="">
        <img src="../../assets/img/my_img3.png" v-if="details.is_collection!==0" alt="">
      </div>
      <div class="fl oDiv5 clipbtn" @click="change" v-if="!$route.query.inviteCode" :data-clipboard-text="copyLink">
        <img src="../../assets/img/detail_share.png" alt="">
      </div>
    </div>
    <div class="comment clear commentMask" @touchmove.prevent="" ref="noTouch" :class="{'positioning':inputBol, isIos: $utils.isIos()}">
      <textarea
        id="comment"
        placeholder="优质评论将被优先展示..."
        class="fl"
        v-model="content"
        maxlength="100"
        ref="commet"
        @focus="inputFocus">
      </textarea>
      <div class="fr oDiv pL30 sendBtn" v-if="content.length===0" @click.stop="release">
        发布
      </div>
      <div class="fr oDiv pL30 btnColor sendBtn" v-if="content.length!==0" @click.stop="release">
        发布
      </div>
      <!-- 评论遮罩层 -->
      <div class="mask" @click.prevent="inputBol = false"></div>
    </div>
    <share-it v-if="shareBol && !$route.query.inviteCode" @shareSuccess='shareSuccess'  @closeShare='closeShare' ref="share" :details="details"></share-it>
  </div>
</template>

<script>
import commentList from './comment-list'
import shareIt from './share-it'
import replyList from './reply-list'
import toutiaoReplyList from './toutiao-reply-list'
import dialogBottom from '../common/dialog-bottom'
import WechatShare from '@/components/home/detail/index'
import Clipboard from 'clipboard'

// import config from './../../assets/js/config'
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'

import { mapState, mapMutations } from 'vuex'
export default {
  name: 'hot-comment',
  props: {
    details: {
      type: Object,
      default: null
    },
    page: {                // 第几页
      type: Number,
      default: 1
    },
    clickNum: {
      type: Number
    },
    articleAd4: {
      type: Object
    },
    shareMethodParams: {
      type: Object
    }
  },
  computed: mapState({
    commonState: state => state.common,
    login_bol: state => state.login_bol,
    share: state => state.share
  }),
  data () {
    return {
      setFocus: false,
      isLoadingReply: 0,
      copyLink: '',
      list: [],            // 评论列表
      per_page: 10,        // 一页多少条
      shareBol: false,     // 分享bol值
      replyBol: false,     // 是否打开回复上拉列表
      replyItem: null,     // 当前回复列表的那个对象
      replyList: [],
      inputBol: false,     // 是否评论
      content: '',         // 评论内容
      total: 0,            // 总评论数
      pageBol: false,      // false位合并评论列表
      replyTotal: 0        // 回复评论总数
    }
  },
  methods: {
    ...mapMutations([
      'set_success_count',
      'set_shareType',
      'setShowShare',
      'set_shareSuccessObj',
      'common_login_box',
      'common_focus'
    ]),
    // 添加上拉加载回复评论
    addlist (list) {
      this.replyList = [...this.replyList, ...list]
    },
    // 登录之前判断是否登录
    isLogin () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
    },
    // 评论定位
    commentTarget () {
      var scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
      if (this.$config.source_style === 6) {
        scrollHeight -= 58
      }
      document.getElementById('ArticleDetails').scrollTop = scrollHeight
    },
    // 分享成功次数自动 +1
    shareSuccess () {
      if (this.$route.query.inviteCode) {
        // this.$store.commit('set_shareCount')
        // this.set_shareType(19)
        // this.setShowShare(true)
      } else {
        this.shareBol = false
        this.getReward()
      }
    },
    // 是否显示奖励弹框 默认显示奖励弹框，分享人则不显示，显示其余的裂变分享弹框
    getReward (isShow = true, callback) {
      this.$api.post('CALLBACK_REWARD', {tag: 'article', aid: this.details.id}, res => {
        if (!isShow) {
          return
        }
        if (res.data.coin <= 0) {
          typeof callback === 'function' && callback(res)
        } else {
          let data = res.data
          data.type = 14
          if (typeof callback === 'function') {
            this.set_shareType(20)
            data.tipText = '将复制的链接发送到微信群，<br>能一键锁粉赚掘金宝'
            this.set_shareSuccessObj({
              type: 20,
              shareSuccessObj: data
            })
            this.setShowShare(true)
          } else {
            this.set_shareType(16)
            this.set_shareSuccessObj({
              type: 16,
              shareSuccessObj: data
            })
            this.setShowShare(true)
          }
        }
      })
    },
    // 显示qq分享引导
    showQQShare () {
      this.set_shareType(14)
      this.setShowShare(true)
    },
    // 发布评论
    release () {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (!this.content.trim()) {
        // 空的话不做任何反应
        this.$toast({
          message: '请填写评论后发布',
          position: 'center',
          duration: 1500
        })
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'bottom',
          duration: 1500
        })
      } else {
        let data = {}
        data.aid = this.$route.params.aid * 1
        data.content = this.content.trim()
        this.$api.post('COMMENT', data, (res) => {
          this.total++
          this.list.unshift({
            cid: res.data.cid,
            avatar: this.commonState.userInfo.avatar,
            nickname: this.commonState.userInfo.nickname,
            reply: 0,
            comment_time: this.$utils.getTimeStamp(),
            content: this.content,
            fabulous: 0,
            is_fabulous: 0
          })
          this.inputBlur()
          this.common_focus(false)
          // 获取完数据以后进行滚动条定位
          this.$nextTick(() => {
            this.$toast({
              message: '评论成功',
              duration: 1500
            })
            this.commentTarget()
          })
          this.content = ''
        })
      }
    },
    // 显示隐藏输入框
    setShowInput () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.inputFocus()
    },
    inputFocus () {
      this.inputBol = true
      let u = navigator.userAgent
      let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)  // ios终端 
      if (isiOS) {
        window.setTimeout(() => {
          window.scrollTo(0, document.body.clientHeight - 10)
        }, 100)
      }
      this.$refs.noTouch.addEventListener('touchmove', e => {}, {passive: false})
    },
    inputBlur () {
      this.inputBol = false
    },
    // 是否收藏
    collectionBnt () {
      let data  = {}
      data.aid = this.$route.params.aid * 1
      data.status = this.details.is_collection === 0 ? 1 : 2
      let txt = data.status === 2 ? '取消收藏成功' : '收藏成功'
      this.$api.post('CLLECT', data, (res) => {
        this.$toast({
          message: txt,
          position: 'middle',
          duration: 1500
        })
        this.details.is_collection = this.details.is_collection === 0 ? 1 : 0
      })
    },
    // 回复按钮
    replyFn (item) {
      if (!item.cid) return  // 无cid不做跳转
      this.replyList = []
      this.replyBol = true     // 打开回复列表
      let url = 'REPLY_LIST'
      this.replyItem = item
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      this.isLoadingReply = 1
      this.$api.get(url, { cid: item.cid }, (res) => {
        if (res.data.top) {
          this.replyList = [...res.data.top, ...res.data.data]
        } else {
          this.replyList = res.data.data
        }
        this.isLoading = false
        this.isLoadingReply = 0
        this.replyTotal = res.data.total
      }, res => {
        this.isLoading = false
        this.isLoadingReply = 0
      })
      setTimeout(() => {
        this.isLoading = false
        this.isLoadingReply = 0
      }, 5000)
    },
    // 手动添加评论回复数据（今日头条）
    getComment ({item, reply}) {
      this.replyItem = item
      this.replyList.unshift(reply)
    },
    // 加载评论列表 ,传过去的aid:this.$route.params.mid
    getComments () {
      let data = {aid: this.$route.params.aid, page: this.page, per_page: this.per_page}
      this.$api.get('COMMENT_LIST', data, (res) => {
        if (res.data.data.length === 0) {
          return
        }
        this.list = res.data.data || [] // 填充数组
        this.total = res.data.total     // 总评论数
      })
    },
    // 回到首页
    toIndex () {
      // this.$router.push({path: '/'})
      this.$emit('upperLevel')
    },
    // 关闭分享组件
    closeShare () {
      this.shareBol = false
    },
    // 加载微信签名
    loadWxSignature () {
      // app端和非微信端不请求签名接口
      if (this.$config.source_style >= 6 || !this.$utils.isWx()) {
        return
      }
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          let sig = res.data
          this.pageUrl = sig.url
          let that = this
          let contentEl = document.createElement('div')
          contentEl.innerHTML = this.details.content
          let content = contentEl.innerText
          var shareContent = {
            title: this.details.title,
            desc: content, // 分享描述
            link: location.href.indexOf('#') > -1 ? location.origin + `?inviteCode=${this.$utils.cache('inviteCode')}&path=${this.$route.path}` : location.herf, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: this.details.img_url && this.details.img_url.length > 0 ? this.details.img_url[0] : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
          }
          // 配置微信签名信息
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: sig.appid, // 必填，公众号的唯一标识
            timestamp: sig.timestamp, // 必填，生成签名的时间戳
            nonceStr: sig.nonceStr, // 必填，生成签名的随机串
            signature: sig.signature, // 必填，签名
            jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
          })
          wx.ready(function () {
            // 分享到微信朋友圈
            wx.onMenuShareTimeline({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function (e) {
                let temp = e.errMsg.split(':')
                if (temp[1] === 'ok') {
                  that.shareSuccess()
                }
                that.shareBol = false
              }
            })
            // 分享到微信好友
            wx.onMenuShareAppMessage({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function (e) {
                let temp = e.errMsg.split(':')
                if (temp[1] === 'ok') {
                  that.shareSuccess()
                }
                that.shareBol = false
              }
            })
            // 分享到qq
            wx.onMenuShareQQ({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.shareSuccess()
              }
            })
          })
          wx.error((res) => {
             
          })
        }
      })
    },
    setComment () {
      this.setFocus = true
    },
    change () {
      let that = this
      if (!this.commonState.login_bol) {
        if (this.$utils.isWx()) {
          this.$utils.wxLogin()
        } else {
          this.$store.commit('common_login_box', true)
        }
        return
      }
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        this.shareBol = true
      } else {
        if (this.$utils.isWx()) {
          if (this.$route.query.inviteCode) {
            let shareSuccessObj = {}
            let shareArc = this.$utils.cache('shareArc') ? JSON.parse(this.$utils.cache('shareArc')) : null
            if (shareArc && shareArc.timestamp * 1 <= this.$utils.getTimeStamp()) {
              shareArc = null
              this.$utils.cache('shareArc', null)
            }
            if (!shareArc) {
              shareSuccessObj = {
                makeRate: 0.5,
                hasGet: 1256
              }
            } else {
              shareSuccessObj = {
                makeRate: shareArc.count * 0.5 + 0.5,
                hasGet: 1256
              }
            }
            this.set_shareSuccessObj({
              type: 2,
              shareSuccessObj
            })
            // 显示引导弹框
            this.set_shareType(2)
            this.setShowShare(true)
          } else {
            this.shareBol = true
          }
        } else {
          // 登录后重置一下复制链接拿到inviteCode
          // 显示qq分享引导
          if (this.$route.query.inviteCode) {
            this.showQQShare()
          } else {
            if (Clipboard.isSupported()) {
              this.cppyLink = location.origin + '/?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
              let clb = new Clipboard('.clipbtn')
              clb.on('success',  (e) => {
                that.getReward(true, res => {
                  this.$toast({
                    message: '文章链接已复制，发送给好友阅读可得金币',
                    duration: 1500
                  })
                })
                clb.destroy()
              })
              clb.on('error',  (e) => {
                this.$toast({
                  message: '复制链接失败了',
                  duration: 1500
                })
                clb.destroy()
              })
            }
          }
        }
      }
    },
    // 初始化
    initData () {
      this.getComments()
    }
  },
  mounted () {
    this.initData()
    this.inviteCode = this.$store.state.inviteCode
    this.copyLink = location.origin + '/?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
        this.copyLink = location.origin + '/?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
      }
    },
    total () {
      this.$emit('total', this.total)
    },
    page () {
      if (this.commonState.isLoading > 0) {
        return
      }
      this.$store.commit('common_set_loading', 1)
      let data = {aid: this.$route.params.aid, page: this.page, per_page: this.per_page}
      this.$api.get('COMMENT_LIST', data, (res) => {
        this.$store.commit('common_set_loading', 0)
        const lists = res.data.data || []
        this.list = [...this.list, ...lists]
        this.total = res.data.total     // 总评论数
        if (this.page >= res.data.last_page) {
          this.$store.commit('common_set_loading', 2)
        }
      })
    },
    'shareMethodParams' (val) {
      if (val.action === 'isLogin') {
        this.setShowInput()
      } else if (val.action === 'collection') {
        this.collectionBnt()
      } else if (val.action === 'commonTarget') {
        this.commentTarget()
      } else if (val.action === 'share') {
        this.change()
      } else if (val.action === 'toIndex') {
        this.toIndex()
      }
    },
    clickNum: function () {
      if (this.$config.source_style >= 6) {
        this.change()
      } else {
        this.showQQShare()
      }
    },
    'share.storeChange': function () {
      this.change()
    },
    details: function () {
      this.loadWxSignature()
    },
    '$route.params.aid' () {
      this.list = []
      this.total = 0
    },
    'inputBol' (flag) {
      if (flag) {
        this.common_focus(true)
        this.$nextTick(() => {
          this.$refs.commet.focus()
        })
      } else {
        this.$refs.commet.blur()
      }
    }
  },
  components: {
    commentList,
    toutiaoReplyList,
    shareIt,
    replyList,
    dialogBottom,
    WechatShare
  }
}
</script>

<style lang="scss" scoped>
#hot-comment{
  width: 100%;
  .recommend{
    padding-left: 21px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .lala{
    width: 750px;
    height: 100%;
    background: #fff;
  }
  img{
    width: 100%;
    height: 100%;
  }
  .comment{
    border-top: 1px #DADADA solid;
    position: absolute;
    position: fixed;
    bottom: 5000px;
    height: 250px;
    background: #fff;
    width: 100%;
    z-index: 12;
    .mask{
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.3);
      position: absolute;
      bottom: 250px;
    }
    .oDiv{
      margin-right: 30px;
      margin-top: 10px;
      background: #959596;
      color: #fff;
      border-radius: 8px;
      padding: 10px 20px;
      text-align: center;
    }
    .btnColor{
      background: #E54256;
    }
    textarea:focus {
      outline: none;
      resize: none;
    }
    textarea {
      width: 690px;
      height: 160px;
      margin-top: 20px;
      margin-left: 28px;
      border-radius: 20px;
      box-sizing: border-box;
      border: none;
      font-size: 30px;
      text-indent: 10px;
      padding: 25px;
      background: #F0F2F1;
    }
    textarea::-webkit-input-placeholder {
      color: #afafaf;
      font-size: 30px;
    }
  }
  .commentMask {
    position: absolute!important;
    &.isIos {
      position: fixed!important;
      .sendBtn {

      }
    }
  }
  .footer{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: -1000px;
    height: 100px;
    background: #fff;
    width: 100%;
    display: flex;
    line-height: 100px;
    .oDiv5{
      width: 56px;
      height: 49px;
    }
    .oDiv3,.oDiv4{
      position: relative;
      width: 49px;
      height: 49px;
      .num{
        position: absolute;
        top: -15px;
        left: 60%;
        font-size: 16px;
        padding: 5px;
        background: red;
        line-height: 18px;
        padding-left: 8px;
        padding-right: 8px;
        color: #fff;
        border-radius: 13px;
      }
    }
    .oDiv4 {
      width: 51px;
    }
    .oDiv3, .oDiv4, .oDiv5 {
      display: flex;
      align-items: center;
      margin: 24px 15px;
    }
    .oDiv2{
      min-width: 390px;
      flex: 1;
      height: 64px;
      margin-top: 18px;
      margin-right: 10px;
      line-height: 64px;
      display: flex;
      align-items: center;
      background: #f5f5f5;
      border-radius: 64px;
      position: relative;
      &::before {
        content: "";
        display: block;
        width: 38px;
        height: 35px;
        margin: 0 16px 0 35px;
        background: url('../../assets/img/editIconGray.png') no-repeat center;
        background-size: 100% 100%;
      }
      .txt{
        color: #666;
        font-size: 24px;
      }
    }
    .oDiv1{
      display: flex;
      align-items: center;
      padding: 0 34px 0 28px;
      height: 100%;
      .backIcon{
        width: 18px;
        display: flex;
        align-items: center;
        height: 35px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .positioning{
    bottom: 0!important;
  }
}
</style>



