<template>
<div class="footer clear test" @click.stop="" :class="{'positioning':!inputBol}">
  <div class="fl oDiv1">
    <div class="backIcon" @click="setLogin('toIndex')">
      <img src="../../assets/img/detail_back.png" alt="">
    </div>
  </div>
  <label class="fl oDiv2" @click="setLogin('isLogin')">
    <!-- <img src="../../assets/img/icon-input.png" alt=""> -->
    <div class="commentLogin" v-if="!commonState.login_bol"></div>
    <div class="txt">写评论...</div>
  </label>
  <div class="fl oDiv3"  @click="setLogin('commonTarget')">
    <img src="../../assets/img/detail_message.png" alt="">
    <div class="num" v-if="total!==0">{{total > 99 ? '99+' : total}}</div>
  </div>
  <div class="fl oDiv4" @click="setLogin('collection')">
    <img src="../../assets/img/detail_collect.png" v-if="details.is_collection===0" alt="">
    <img src="../../assets/img/my_img3.png" v-if="details.is_collection!==0" alt="">
  </div>
  <div class="fl oDiv5 clipbtn" @click="setLogin('share')">
    <img src="../../assets/img/detail_share.png" alt="">
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      inputBol: false,
      count: 0
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  props: {
    total: {
      type: Number
    },
    details: {
      details: {
        type: Object
      }
    }
  },
  methods: {
    setLogin (action) {
      this.$emit('shareMethod', {count: ++this.count, action})
    }
  }
}
</script>
<style lang="scss" scoped>
.footer{
  border-top: 1px #DADADA solid;
  position: fixed;
  bottom: 0;
  height: 100px;
  background: #fff;
  width: 100%;
  display: flex;
  line-height: 100px;
  img {
    width: 100%;
  }
  .oDiv5{
    width: 56px;
    height: 49px;
  }
  .oDiv3,.oDiv4{
    position: relative;
    width: 49px;
    height: 49px;
    .num{
      position: absolute;
      top: -15px;
      left: 60%;
      font-size: 16px;
      padding: 5px;
      background: red;
      line-height: 18px;
      padding-left: 8px;
      padding-right: 8px;
      color: #fff;
      border-radius: 13px;
    }
  }
  .oDiv4 {
    width: 51px;
  }
  .oDiv3, .oDiv4, .oDiv5 {
    display: flex;
    align-items: center;
    margin: 24px 15px;
  }
  .oDiv2{
    min-width: 390px;
    flex: 1;
    height: 64px;
    margin-top: 18px;
    margin-right: 10px;
    line-height: 64px;
    display: flex;
    align-items: center;
    background: #f5f5f5;
    border-radius: 64px;
    position: relative;
    &::before {
      content: "";
      display: block;
      width: 38px;
      height: 35px;
      margin: 0 16px 0 35px;
      background: url('../../assets/img/editIconGray.png') no-repeat center;
      background-size: 100% 100%;
    }
    .txt{
      color: #666;
      font-size: 24px;
    }
  }
  .oDiv1{
    display: flex;
    align-items: center;
    padding: 0 34px 0 28px;
    height: 100%;
    .backIcon{
      width: 18px;
      display: flex;
      align-items: center;
      height: 35px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



