<template>
  <div class="scrollBox">
    <div class="topTxt" v-if="$config.source_style === 6 || $config.source_style === 7">
      <div class="imgBox" @click="backToIndex">
        <img src="../../assets/img/black-left.png" alt="">
      </div>
      文章详情
      <div class="right shareIconBlack" @click="change()"></div>
    </div>
    <div id="ArticleDetails" ref="ArticleDetails" :class="{hasDownload: $route.query.inviteCode, hasAd: articleAd4 && articleAd4.isShow}">
      <div v-if="$config.source_style === 6 || $config.source_style === 7" class="pT90"></div>
      <div class="details-icon" v-if="details==null">
        <img src="../../assets/img/details-icon.gif" alt="">
      </div>
      <div class="details-txt" v-if="details==null">
        <img src="../../assets/img/details-txt.gif" alt="">
      </div>
      <!-- 屏蔽了左右滑动事件 -->
      <div ref="acturallyContent" id="acturallyContent">
        <div v-if="details!=null">
          <download v-if="$route.query.inviteCode" class="topDownload"></download>
          <div class="hearder">
            <div class="font-600 f40 lh56 articleTitle color3A">{{details.title}}</div>
            <div class="attention clear" :class="{isAndroid: $utils.isAndroid()}">
              <div class="fl p1">
                <img :src="details.author_logo"
                alt=""
                onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';"
                @click="toMedia()">
              </div>
              <div class="fl p2" @click="toMedia()">
                <div class="name f28 font-600">{{details.author}}</div>
                <div class="time clear f24 mT10">
                  <div class="fl mR12 colorBase">{{$utils.timeFormat(details.publish_time * 1000)}}</div>
                  <div class="fl colorBase"> 阅读  {{$utils.thousandRead(details.read_num)}}</div>
                </div>
              </div>
              <div class="fr followBtn followBtn_center"  v-show="details.is_follow === 1 && details.mid > 0" v-if="!($utils.isQQ() && $route.query.inviteCode)" @click.stop="isFollow">√ 已关注</div>
              <div class="fr followBtns followBtn_center" v-show="details.is_follow !== 1  && details.mid > 0"  v-if="!($utils.isQQ() && $route.query.inviteCode)" @click.stop="isFollow">+ 关 注</div>
            </div>
          </div>
          <!-- 文章内容 -->
          <div ref="detailsContent" :class="{'fullText':expandBol}">
            <div class="details-content" v-html="details.content"></div>
            <div class="source clear f28 mT30">
              <div class="fl">来源：{{details.source}}</div>
            </div>
          </div>
          <!-- 展开全文 1000px -->
          <div class="expand clear" ref="expand" v-if="expandBol">
            <div class="gif_Button" @click="expandBtn">
              <div class="iconSize">
                <img src="../../assets/img/details/pull_down.gif" alt="git图标">
              </div>
            </div>
            <div class="expand_button" @click="toDownload">打开APP阅读全文</div>
          </div>
          <!-- 第三个广告位置 -->
          <div ref="adEl1" class="recommendList3" v-if="articleAd && articleAd.isShow">
            <div class="rect"></div>
            <ad-loop  :view="articleAd" :position="articleAd.position"></ad-loop>
          </div>
          <news-list
            :list="list"
            v-if="list.length > 0"
            :total='total'
            @setAdClicks="setAdClicks"
            @upDate="upDate"
            @isgoblack="isgoblack=false"
            class="wonderful">
          </news-list>
          <!-- 跳转地址 第三个广告 -->
          <div class="recommendList" ref="adEl3" v-if="articleAd3 && articleAd3.isShow">
            <ad-loop :view="articleAd3" :position="articleAd3.position"></ad-loop>
          </div>
        </div>
        <hot-comment :shareMethodParams="shareMethodParams"  @upperLevel="upperLevel" :articleAd4="articleAd4" :clickNum='clickNum' :details="details" :page="page" @pageOne="page=1" @total="getTotal" ></hot-comment>
      </div>
      <!-- <loop-list :class="{bottomTip: commonState.login_bol && !$route.query.inviteCode}" v-if="articleList.length > 0 && $route.query.inviteCode" @showQQ='change' :list='articleList'></loop-list> -->
      <transition name="dropTxt">
        <download :class="{bottomTip: commonState.login_bol && !$route.query.inviteCode, isApp: $config.source_style >= 6}" v-if="$route.query.inviteCode && showDownload && !(articleAd4 && articleAd4.isShow)"></download>
      </transition>
    </div>
    <AdLoop v-if="articleAd4 && articleAd4.isShow" class="arcAd4" :view="articleAd4" :position="4" @closeFn="closeFn"></AdLoop>
    <articleMd v-if="isShow" :coinParams="coinParams"></articleMd>
    <articleLead v-if="fxHint && !$route.query.inviteCode"></articleLead>
    <ExitConfirm v-if="showConfirm" :carInfo='shareCarInfo' @closeConfirm='closeConfirm'></ExitConfirm>
    <!-- 只做展示用 iOS滑动时跟着一起滚动 -->
    <common-comment @shareMethod="shareMethod" v-if="details && $config.source_style === 6 && !commonState.isFocus" :details="details" :total="total"></common-comment>
  </div>
</template>

<script>
import NewsList from '../common/news-list'
import HotComment from './hot-comment'
import HeaderNav from '../common/header-nav'
import loopList from '@/components/share/article_loop'
import articleMd from '../common/article-md'
import articleGain from './article-gain'
import articleLead from './article-lead'
import { mapState, mapMutations } from 'vuex'
import ExitConfirm from '@/components/share/share_page1/exit_tip'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import AdLoop from './adList'
import download from '@/components/common/download_article'
import CommonComment from '@/components/common/comment_article'

export default {
  name: 'ArticleDetails',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    var that = this
    return {
      showDownload: false,
      showOthers: false,
      articleAd1: null,
      articleAd3: null,
      articleAd4: null,
      shareMethodParams: {},
      showComment: false,
      swiperOptions: {
        el: that.$refs.swiperContainer,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        loop: true
      },
      isClick: false,   // 防止多次点击
      others: [{
        img_url: 'http://35.220.131.35/images/Jjb.gif',
        link: 'http://t.aixianggua.com/un1447o500/2_569702786623614976_597961088461451264_3472f0bbba332c540442cee2f17a3d12'
      }],
      count: 0, // 秒数
      msg: '文章详情',
      articleAd: null,
      isScrollAd: false,
      hasReward: true,
      showConfirm: false,
      shareCarInfo: {},
      list: [],
      details: null,      // 是否显示下面滚动窗口
      isgoblack: true,     // 是否需要监听aid
      toScrollBol: false,  // 是否需要滚动到指定位置
      page: 1,             // 评论第几页
      total: 0,            // 评论总条数
      detailsArr: [],      // 存储跳转过的文章
      pageNum: 0,          // 可以分多少页
      expandBol: false,    // 是否有展开全文按钮
      oldHeight: true,     // 刷新文章是否保持滚动条回顶部
      timestamp: 0,      // 时间戳
      articleList: [],
      clickNum: 0,           // 点击下面文章数量改变
      isShow: false,         // 首次阅读显示
      fxHint: false,
      coinParams: {}
    }
  },
  methods: {
    ...mapMutations([
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_focus'
    ]),
    // iOS分享操作
    shareMethod (e) {
      this.shareMethodParams = e
    },
    toDownload () {
      this.$utils.downLoadExtra()
    },
    // 关闭广告
    closeFn (id) {
      this.$set(this.articleAd4, 'isShow', false)
    },
    toMedia () {
      if (this.details.mid > 0 && !(this.$route.query.inviteCode && this.$utils.isQQ())) {
        this.$router.push({path: '/MediaNumber/' + this.details.mid})
      }
    },
    // 获取广告内容
    getAd () {
      // 位置1广告
      this.$api.get('GET_AD', {id: 1}, res => {
        if (res.data) {
          this.articleAd = this.$utils.getCurrentAd(res.data, 1)
        }
      })
      // 位置3广告
      this.$api.get('GET_AD', {id: 3}, res => {
        if (res.data) {
          this.articleAd3 = this.$utils.getCurrentAd(res.data, 3)
        }
      })
      // 位置4广告
      this.$api.get('GET_AD', {id: 4}, res => {
        if (res.data) {
          if (res.data && res.data.length > 0) {
            let obj =  this.$utils.getCurrentAd(res.data, 4)
            obj.showDelete = true
            this.articleAd4 = obj
          }
        }
      })
    },
    closeAd () {
      this.articleAd = null
    },
    // 关闭弹框时做的操作
    closeConfirm ({action}) {
      if (action === 'confirm') {
        this.showConfirm = false
      } else if (action === 'cancel') {
        this.showConfirm = false
        this.hasReward = false
        this.$router.replace('/')
      }
    },
    // 获取车辆信息
    getCarInfo () {
      this.$api.get('SHARE_GUILDBROWERS', {}, res => {
        this.shareCarInfo = res.data
      })
    },
    // 返回
    backToIndex () {
      // 如果400ms之后没反应，则主动跳转至首页
      if (this.$route.matched.length <= 1) {
        this.$router.replace('/')
      } else {
        this.$router.go(-1)
      }
    },
    // 返回上一级
    upperLevel () {
      this.backToIndex()
    },
    change () {
      this.clickNum++
    },
    // 调取获得文章奖励的接口
    getReward () {
      if (this.$utils.isGetReard(this.$route.params.aid)) {
        this.$api.get('ARCREWARD_TIP', {aid: this.$route.params.aid, type: 'arc'}, res => {
          // res.data.first_tips = 1
          if (res.data.coin > 0) {
            if (!this.$utils.cache('first_tips')) {
              this.toggleIs(res)
            } else if (this.$config.source_style >= 6) {
              let shareSuccessObj = Object.assign({type: 1, tipText: res.data.extra_coin ? `用掘金宝App读文章额外多赚了${res.data.extra_coin}金币` : ''}, res.data)
              if (!this.$utils.cache('isFirstRead')) {
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.$utils.cache('isFirstRead', 1)
              } else {
                this.set_shareSuccessObj({
                  type: 7,
                  shareSuccessObj
                })
                this.set_shareType(7)
              }
              this.setShowShare(true)
            } else {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App读文章赚更多金币`}, res.data)
              this.set_shareSuccessObj({
                type: 7,
                shareSuccessObj
              })
              this.set_shareType(7)
              this.setShowShare(true)
            }
            // 已获得奖励设置无奖励
            this.$utils.setArticle(this.$route.params.aid, 'article')
          }
        })
      }
    },
    // 点击展开全文
    expandBtn () {
      this.showDownload = true
      let h = this.$refs.ArticleDetails.scrollTop   // 记录滚动条高度
      this.expandBol = false
      this.$nextTick(() => {
        this.$refs.ArticleDetails.scrollTop = h
        if (this.timer) {
          this.timer = null
          return
        }
        this.timestamp = this.$utils.getTimeStamp()
        this.timer = setInterval(() => {
          this.count++
          if (this.count >= 30) {
            this.getReward()
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      })
    },
    // 设置广告点击数
    setAdClicks (id) {
      // 防止多次点击
      if (this.isClick) {
        return
      }
      this.isClick = true
      let data = {
        aid: this.$route.params.aid,
        id: id,
        from: this.$utils.isWx() ? 'weixin' : 'wap'
      }
      this.$api.get('ADCLICK', data, res => {})
    },
    // 设置广告浏览数
    setAdBrowsers (id) {
      let data = {
        aid: this.$route.params.aid,
        id: id,
        from: this.$utils.isWx() ? 'weixin' : 'wap'
      }
      this.$api.get('ADREAD', data, res => {})
    },
    // 获取上拉列表
    upList () {
      let adEl2 = document.querySelector('.adList')
      if (this.$refs.adEl3 && this.$refs.ArticleDetails.scrollTop > (this.$utils.getElposition(this.$refs.adEl3) - window.screen.height + 120) && !this.isScrollAd3) {
        this.isScrollAd3 = true
        this.setAdBrowsers(this.articleAd3.id)
      } else if (adEl2 && this.$refs.ArticleDetails.scrollTop > (this.$utils.getElposition(adEl2) - window.screen.height + 120) && !this.isScrollAd2) {
        this.isScrollAd2 = true
        this.setAdBrowsers(this.articleAd2.id)
      } if (this.$refs.adEl1 && this.$refs.ArticleDetails.scrollTop > (this.$utils.getElposition(this.$refs.adEl1) - window.screen.height + 120) && !this.isScrollAd) {
        this.isScrollAd = true
        this.setAdBrowsers(this.articleAd.id)
      }
      if (this.$refs.expand) {
        if (this.$refs.ArticleDetails.scrollTop > (this.$utils.getElposition(this.$refs.expand) + this.$refs.expand.offsetHeight)) {
          this.showDownload = true
        } else {
          this.showDownload = false
        }
      }
      if (this.$refs.acturallyContent.offsetHeight - this.$refs.ArticleDetails.scrollTop < 1100 && this.commonState.isLoading === 0) {
        this.page++
        if (!this.$utils.cache('everyTime')) { // 没有时间调用
          if (!this.$route.query.inviteCode) {
            this.getDateNew()
          }
        } else { // 有时间做对比 相同一天
          let datas = JSON.parse(this.$utils.cache('everyTime'))
          if (datas.time < new Date().getDate() && datas.zt) {
            this.getDateNew()
          } else if (datas.time === new Date().getDate() && !datas.zt) {
            this.getDateNew()
          }
        }
      }
    },
    // 获取详情
    getDetails () {
      this.getAd()
      this.common_focus(false)
      let data = {
        aid: this.$route.params.aid
      }
      this.$store.commit('common_set_loading', 0)
      if (this.$route.query.call) {
        data.call = this.$route.query.call
      }
      this.$api.get('DETAIL', data, (res) => {
        if (res.data.coin) {
          let shareSuccessObj = {
            coin: res.data.coin,
            type: 15
          }
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj
          })
          this.set_shareType(7)
          this.setShowShare(true)
        }
        res.data.content = res.data.content.replace(this.$reg['LINECHANGE'], '').replace(this.$reg['ONEBLACK'], '><').replace(/<img\s/g, '<img preview=1 ')
        this.details = res.data
        this.$nextTick(() => {
          // this
          this.$previewRefresh()
          this.$refs.ArticleDetails.scrollTop = 0
          let article = document.getElementsByClassName('details-content')
          let imgs = article[0].getElementsByTagName('img')
          let articleBox = article[0].firstChild
          if (articleBox) {
            if (this.$config.source_style >= 6 || this.$utils.isIos()) {
              if (this.$config.source_style === 6 || this.$utils.isIos()) {
                let width = articleBox.offsetWidth
                this.$utils.setElHeight(width, imgs, 0)
              }
            } else {
              this.$utils.setIframeImgs(imgs)
            }
          }
        })
        // 没有详情时跳转404页面
        if (!this.details) {
          this.$router.replace('/notFound')
        }
        document.title = res.data.title
        this.$utils.cache('inviteCode', res.data.invitation)
        if (this.oldHeight) {    // 刷新文章是否保持滚动条回顶部
          window.scrollTo(0, 0)
        }
        this.oldHeight = true
        this.$nextTick(() => {
          if (this.$refs.detailsContent.offsetHeight > 1000) {
            if (this.$route.query.inviteCode) {
              this.expandBol = true
            }
            this.isgoblack = true         // 是否需要监听aid
            if (this.toScrollBol) {
              if (this.detailsArr && this.details.length > 0) {
                window.scrollTo(0, this.detailsArr[0].scroll)
                this.toScrollBol = false
                this.detailsArr.splice(0, 1)
              }
            }
          }
        })
      }, res => {
        this.$router.replace('/')
      })
    },
    // 获取精彩推荐列表
    getRecommend () {
      let data = {aid: this.$route.params.aid}
      let arcTop = this.$utils.cache('arcTop')
      if (!arcTop) {
        data.is_first = 1
      } else {
        if (arcTop < new Date().getTime()) {
          data.is_first = 1
        }
      }
      this.$api.get('AID_RECOMMEND', data, (res) => {
        this.list = res.data || []
        this.getListAd()
        if (data.is_first) {
          this.$utils.cache('arcTop', new Date().getTime() + 3600 * 24 * 1000)
        }
      })
    },
    // 获取列表内广告
    getListAd () {
      this.$api.get('GET_AD', {id: 2}, res => {
        if (res.data && this.$utils.getCurrentAd(res.data)) {
          this.articleAd2 = this.$utils.getCurrentAd(res.data, 2)
          if (this.articleAd2) {
            this.setAdBrowsers(this.articleAd2.id)
            // this.articleAd2.title = '微信用户谢谢'
            // this.articleAd2.show = 0
            // this.articleAd2.img = [this.articleAd2.img[0]]
            this.list = [...this.list.slice(0, 3), this.articleAd2, ...this.list.slice(3, 5)]
          }
        }
      })
    },
    // 是否关注
    isFollow () {
      let data = {}
      data.mid = this.details.mid * 1
      data.status = this.details.is_follow === 0 ? 1 : 2
      this.$api.post('IS_FOLLOW', data, (res) => {
        this.details.is_follow = this.details.is_follow === 0 ? 1 : 0
        if (this.details.is_follow === 0) {
          this.$toast({
            message: '已取消关注',
            duration: 1500
          })
        } else {
          this.$toast({
            message: '已关注',
            duration: 1500
          })
        }
        this.$utils.refreshAttention(data.status, data.mid)
      })
    },
    // 获取总评论条数
    getTotal (total) {
      this.total = total
      this.pageNum = Math.ceil(this.total / 10)
    },
    // 离开文章回调
    leave (aid) {
      if (this.$utils.cache('user_token')) {
        let data = {aid, starttime: this.timestamp, type: 'arc'}
        this.$api.get('LEAVE', data, (res) => {
        })
      }
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    upDate () {
      let data = {}
      data.aid = this.$route.params.aid
      data.scroll = this.$utils.getScrollTop()
      this.detailsArr = [data, ...this.detailsArr]
      this.initDate()
    },
    getContent () {
      if (this.articleList.length === 0 && this.$route.query.inviteCode) {
        this.$api.get('ARTICLE_LOOP_DATA', {invitation_code: this.$route.query.inviteCode}, res => {
          this.articleList = res.data
        })
      }
    },
    // 初始化阅读数据
    initDateReward () {
      clearInterval(this.timer)
      this.timer = null
      this.count = 0
    },
    // 初始化
    initDate () {
      this.articleAd = null
      this.isScrollAd = false
      this.isScrollAd2 = false
      this.isScrollAd3 = false
      this.isClick = false
      this.showDownload = false
      this.getDetails()
      this.getRecommend()
      this.getContent()
      if (this.$route.query.inviteCode) {
        
      }
    },
    getDateNew () {
      this.fxHint = true
      setTimeout(() => {
        this.fxHint = false
        this.$utils.cache('everyTime', JSON.stringify({'time': new Date().getDate(), 'zt': 1}))
      }, 5000)
    },
    toggleIs (res) {
      this.coinParams = res.data
      this.coinParams.type = 'article'
      this.$utils.cache('first_tips', 1)
      this.isShow = true
    },
    back () {
      // if (this.$utils.isWx() || this.$utils.isQQ()) {
      //   this.showConfirm = true
      //   this.hasReward = false
      //   this.$utils.pushHistory()
      // }
    },
    swipeRight () {
      this.$router.replace('/')
    },
    routeBack () {
      window.open(this.$config.downloadUrl)
      this.$utils.cache('hasAddBack', true)
      window.removeEventListener('popstate', this.routeBack)
    }
  },
  mounted () {
    this.aid = this.$route.params.aid
    this.$utils.cache('rewardId', this.aid)
    this.initDate()
    this.$preview.on('close', res => {
      this.$preview.self = null
    })
    if (!this.$route.query.inviteCode) {
      this.expandBtn()
    } else {
      if (!this.$utils.cache('hasAddBack')) {
        this.$utils.pushHistory()
        window.addEventListener('popstate', this.routeBack, false)
      }
      this.getCarInfo()
    }
    this.$refs.ArticleDetails.addEventListener('scroll', this.upList)
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.oldHeight = false
        let h = this.$utils.getScrollTop()   // 记录滚动条高度
        this.initDate()
        this.$nextTick(() => {
          window.scrollTo(0, h)
        })
      }
    },
    '$route.params.aid' (a, b) {
      this.$utils.cache('rewardId', a)
      this.count = 0
      this.page = 1
      this.leave(b)
      if (!this.$route.query.inviteCode) {
        this.expandBtn()
      }
      this.timestamp = this.$utils.getTimeStamp()
      if (this.isgoblack) {
        this.toScrollBol = true
        this.initDate()
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.$preview.self) {
      this.$preview.self.close()
      this.$preview.self = null
      next(false)
    } else {
      next()
    }
  },
  beforeDestroy () {
    this.leave(this.$utils.cache('rewardId'))
  },
  components: {
    NewsList,
    HotComment,
    HeaderNav,
    loopList,
    articleMd,
    articleGain,
    articleLead,
    ExitConfirm,
    swiper,
    swiperSlide,
    AdLoop,
    download,
    CommonComment  // 用于iOS显示滑动问题
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
.scrollBox {
  .arcAd4 {
    position: fixed;
    bottom: 100px;
  }
  .topDownload {
    position: static;
    box-shadow: 0 -1px 5px 1px #E5E5E5;
  }
  // 广告
  .recommendList3 {
    width: 100%;
    margin: 25px 0 20px;
    .rect {
      height: 20px;
      background: #f5f5f5;
      margin-bottom: 25px;
    }
    a {
      display: block;
      height: 100%;
      height: 190px;
    }
    .swiperOuter {
      height: 100%;
    }
    .iconSize {
      height: 100%;
    }
  }
  .topTxt{
    line-height: 90px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #eee;
    text-align: center;
    position: fixed;
    width: 100%;
    z-index: 1;
    .imgBox{
      position: absolute;
      width: 20px;
      top: 50%;
      transform: translateY(-50%);
      padding: 10px;
      left: 20px;
      height: 35px;
      line-height: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .right{
      position: absolute;
      right: 30px;
      top: 0;
      height: 90px;
    }
  }
}
#ArticleDetails{
  overflow: auto;
  height: 100vh;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  .details-icon{
    width: 600px;
    height: 450px;
    margin: 250px auto 0px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .details-txt{
    width: 150px;
    height: 54px;
    margin: 100px auto 0px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  
  .fullText{
    transition: all .3s;
    height: 2000px;
    overflow: hidden;
  }
  .expand{
    text-align: center;
    font-size: 28px;
    line-height: 36px;
    color: #FF7607;
    position: relative;
    margin: -140px auto 20px;
    flex-flow: column wrap;
    width: 704px;
    height: 209px;
    background: url('../../assets/img/details/white_gradient.png') no-repeat center;
    background-size: 100% 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .gif_Button {
      width: 80px;
      height: 80px;
      vertical-align: bottom;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      padding-bottom: 20px;
      .iconSize {
        width: 39px;
        height: 39px;
        display: inline-block;
        vertical-align: middle;
      }
    }
    .box{
      position: absolute;
      width: 100%;
      height: 30px;
      background: linear-gradient(to top, #fff, rgba(255,255,255,0.3));;
      top: -50px;
    }
    .expand_button {
      width: 692px;
      height: 65px;
      border-radius: 65px;
      color: #fff;
      font-size: 26px;
      text-align: center;
      line-height: 65px;
      background: #FF7607;
    }
    .txt{
      margin-left: 290px;
    }
    .img{
      width: 18px;
      height: 12px;
      margin-left: 10px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .abc{
    touch-action: pan-y !important;
  }
  .recommend{
    padding-left: 34px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .content{
    width: 100%;
    padding-left: 34px;
    padding-right: 22px;
    box-sizing: border-box;
  }
  .source{
    padding-left: 34px;
    color: #999;
    line-height: 40px;
    margin-top: 20px;
    margin-bottom: 24px;
  }
  .hearder{
    padding-left: 34px;
    padding-right: 34px;
    padding-top: 32px;
    padding-bottom: 40px;
    .attention{
      margin-top: 24px;
      .followBtn,.followBtns{
        font-size: 26px;
        width: 130px;
        height: 46px;
        line-height: 46px;
        color: #fff;
        border-radius: 8px;
        text-align: center;
        background: #bdbcbc;
      }
      &.isAndroid {
        .followBtn_center {
          box-sizing: border-box;
          padding-top: 4px;
        }
      }
      .followBtns{
        background: #e54256;
      }
      .hook{
        background: #BDBCBC;
      }
      .p3{
        width: 130px;
        height: 46px;
        border-radius: 8px;
        margin-top: 10px;
        vertical-align: middle;
        color: #fff;
        .imgs{
          width: 26px;
          height: 26px;
          margin-top: 6px;
          margin-left: 10px;
          margin-right: 10px;
        }
        img{
          width: 100%;
          height: 100%;
        }
      }
      .p2{
        .time{
          color: #999;
        }
      }
      .p1{
        width: 62px;
        height: 62px;
        margin-right: 10px;
        img{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
    }
  }
  .bottomTip{
    bottom: 100px;
    transition: all .3s;
  }
  .showTop {
    bottom: auto;
    top: 0;
    box-shadow: 0 4px 5px rgba($color: #000000, $alpha: 0.08);
    &.isApp {
      top: 88px;
    }
  }
}
</style>



